$(function(){
  console.log("Start Web App...");

  $("#btnDelete").on("click", function() {
    var boardNo = window.location.pathname.split("/")[3];
    console.log(boardNo);
    /**
     * DELETE Board Article
     */
    $.ajax({
      url : "/board/delete",
      type : "POST",
      dataType : "JSON",
      data : {"board_no" : boardNo},
      success : function(res) {
        console.log(res); 
      }
    })
    .done(function() {
      alert("  .");
    })
    .fail(function() {
      alert("something went wrong...");
    })
  })
})